<template>
	<div id="bookCategory">
		<ul class="category_list">
			<li
				v-for="(list, index) in $store.state.arrBookCategory"
				:key="list.TLEFT"
				@click="getBookCategory(list.TREEID, index)"
			>
				<div class="title">{{ list.TITLE }}</div>
			</li>
		</ul>
		<Modal v-if="isModalShow" @close="isModalShow = false">
			<div slot="header">
				{{ $store.state.arrBookCategory[selectNum].TITLE }}
			</div>
			<div slot="body" class="book_list_wrap">
				<div
					class="book_list"
					v-for="(book, index) in arrBookList"
					:key="index"
				>
					<div class="img_info">
						<img
							:src="`https://www.ezcampus.kr:444/File/Details/${book.BOOK_UKEY}.jpg?ukey=${book.BOOK_UKEY}`"
							alt=""
						/>
					</div>
					<div class="book_info">
						<div class="name">{{ book.BOOK_TITLE }}</div>
						<div class="auth">{{ book.BOOK_AUTHOR }}</div>
						<div class="publisher">{{ book.BOOK_PUBLISHER }}</div>
						<div class="date">{{ book.BOOK_PUBLISH_DT.split("T")[0] }}</div>
					</div>
				</div>
			</div>
			<div slot="footer">
				<div class="btn_more_area">
					<button type="button" class="btn_more" @click="getBookCategoryMore()">
						더보기
					</button>
				</div>
			</div>
		</Modal>
	</div>
</template>

<script>
import { fetchBookListParams } from "@/api/index";
import Modal from "@/components/common/ModalPopup.vue";
import { instance } from "@/api/index";
export default {
	data() {
		return {
			isModalShow: false,
			treeidNum: 0,
			selectNum: 0,
			arrBookList: [],
			pagingNum: 1,
		};
	},
	methods: {
		async getBookCategory(treeid, index, paging = 1) {
			try {
				const { data } = await fetchBookListParams(treeid, paging);
				this.isModalShow = !this.isModalShow;
				this.treeidNum = treeid;
				this.selectNum = index;
				this.arrBookList = data;
				this.pagingNum = 1;
			} catch (error) {
				console.log(error.response);
			}
		},
		getBookCategoryMore(treeid) {
			instance
				.get("api/Book", {
					params: {
						c: this.treeidNum,
						p: ++this.pagingNum,
					},
				})
				.then(res => {
					const response = res.data;
					response.forEach(element => {
						this.arrBookList.push(element);
					});
				})
				.catch(e => console.log(e));
		},
	},
	components: {
		Modal,
	},
	created() {
		this.$store.dispatch("FETCH_BOOKCATEGORY");
	},
};
</script>

